.container{
    height: 50vh;
  }
  
  .container{
    position: relative;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;


    img{
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        object-fit: contain;
      }
  }

  
  

  .static-txt{
    color: #fff;
    font-size: 60px;
    font-weight: 400;
    font-family: 'Audiowide' !important;

    @media all and (max-width: 480px){
      font-size: 50px;
   }
  @media all and (min-width: 4810px) and (max-width: 768px){
      font-size: 60px;
   }
  }


  .dynamic-txts{
    // margin-left: 15px;
    height: 90px;
    line-height: 90px;
    overflow: hidden;
    padding-left: 0;
    font-family: 'Audiowide' !important;

    @media all and (max-width: 480px){
      font-size: 50px;
   }
  @media all and (min-width: 4810px) and (max-width: 768px){
      font-size: 60px;
   }

  }


  .dynamic-txts li{
    list-style: none;
    color: rgb(132, 194, 255);
    font-size: 68px;
    font-weight: 500;
    position: relative;
    top: 0;
    animation: slide 12s steps(4) infinite;


    @media all and (max-width: 480px){
      font-size: 50px;
   }
  @media all and (min-width: 4810px) and (max-width: 768px){
      font-size: 60px;
   }
  }


  @keyframes slide {
    100%{
      top: -360px;
    }
  }
  .dynamic-txts li span{
    position: relative;
    // margin: 5px 0;
    // line-height: 86px;
  }
  .dynamic-txts li span::after{
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    height: 101%;
    width: 100%;
    // background: #ffffff;
    border-left: 2px solid rgb(132, 194, 255);
    animation: typing 3s steps(10) infinite;
  }
  @keyframes typing {
    40%, 60%{
      left: calc(100% + 30px);
    }
    100%{
      left: 0;
    }
  }

.hero_container{
    // position: relative;
    
    width: 90vw;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    height: 80vh;
    object-fit: contain;


    img{
        position: absolute;
        width: 101%;
        height: 101%;
        top: 0;
        left: 0;
        object-fit: contain;
        overflow: hidden;
        align-self: center;
        justify-self: center;
        // margin-top: 2rem;
      }
  }
.show{
  visibility: visible;
}

.hide{
  visibility: hidden;
}

.indicatorcover{
  display:flex;
  justify-content:center;
  align-items:center;
  margin-top: 1rem;
}
.indicator{
  position:relative;
  width:25px;
  height:25px;
  
  transform:rotate(45deg);
  span{
    position:absolute;
    left:0;
    top:0;
    width:100%;
    height:100%;
    box-sizing:border-box;
    border:none;
    border-bottom:3px solid #fff;
    border-right:3px solid #fff;
    animation:animate 1s linear infinite;
    &:nth-child(1){
      top:-10px;
      left:-10px;
      animation-delay:0s;
    }
    &:nth-child(2){
      top:-5px;
      left:-5px;
      animation-delay:0.2s;
    }
    &:nth-child(3){
      top:0;
      left:0;
      animation-delay:0.4s;
    }
    &:nth-child(4){
      top:5px;
      left:5px;
      animation-delay:0.6s;
    }
    &:nth-child(5){
      top:10px;
      left:10px;
      animation-delay:0.8s;
    }
  }
}

@keyframes animate{
0%{
  border-color:#fff;
  transform:translate(0,0);
}
 20%{
  border-color:#fff;
   transform:translate(5px,5px);
}
 20.1%,100%{
  border-color:rgb(132, 194, 255);
}
}





