*{
  user-select: none;
}
body{
  overflow-x: hidden;
  scroll-behavior: smooth;
}
:root {
  --color-primary: #191d2b;
  --color-secondary: rgb(132, 194, 255);
  --color-white: #FFFFFF;
  --color-black: #000;
  --color-grey0: #f8f8f8;
  --color-grey-1: #dbe1e8;
  --color-grey-2: #b2becd;
  --color-grey-3: #6c7983;
  --color-grey-4: #454e56;
  --color-grey-5: #2a2e35;
  --color-grey-6: #12181b;
  --br-sm-2: 14px;
  --box-shadow-1: 0 3px 15px rgba(0,0,0,.3);
}

::-webkit-scrollbar{
  width: 15px;
}

::-webkit-scrollbar-track{
  background: #111;
}



::-webkit-scrollbar-thumb{
  background: rgb(132, 194, 255);
  height: 200px;
  border-radius: 25px;

}
.dark{
  background-color: rgb(0, 2, 22);
  li span::after{
    background: rgb(0, 2, 22);
  }
}

.light{
  background-color: rgb(126, 104, 186);
  li span::after{
    background: rgb(126, 104, 186);
  }
}

.txt_dark{
  color: rgb(0, 2, 22);
}

.txt_light{
  color: rgb(126, 104, 186);
}

.cursor_dark{
  background-color: rgba(255, 255, 255, 1);
}
.cursor_light{
  background-color: rgba(17, 17, 17, 1);
}

.add{
  display: block;
}
.remove{
  display: none;
}

